html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #F3F2ED;
  font-family: 'Raleway-Regular', sans-serif;
  color: white;
  margin: 0;
  padding: 0;
  text-align: center;
}

@font-face {
  font-family: 'Raleway-Regular';
  src: local('Raleway-Regular'), url(/React-Weather-App/static/media/Raleway-Regular.580d0778.ttf) format('truetype');
}
.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
  height: 400px;
  width: 320px;
  padding: 15px 25px;
  box-shadow: 0 1px 8px rgba(0,0,0,0.4);
  position: relative;
  top: 50%;
  -webkit-transition: ease-in 0.4s;
  transition: ease-in 0.4s;
}

.card>h3,p {
  margin: 5px 0;
}

.a01d,
.a02d,
.a03d,
.a04d,
.a09d,
.a10d,
.a11d,
.a13d,
.a50d {
  background: -webkit-linear-gradient(72deg, rgb(51, 155, 184) 17%, rgba(255,190,0,1) 100%);
  background: linear-gradient(18deg, rgb(51, 155, 184) 17%, rgba(255,190,0,1) 100%);
}

.a01n,
.a02n,
.a03n,
.a04n,
.a09n,
.a10n,
.a11n,
.a13n,
.a50n {
  background: -webkit-linear-gradient(72deg, rgba(182,136,252,1) 0%, rgba(73,43,176,1) 100%);
  background: linear-gradient(18deg, rgba(182,136,252,1) 0%, rgba(73,43,176,1) 100%); 
}

.card-icon {
    padding-top: 15px; 
    width: 60%;
}

.card-line {
  border-bottom: 3.5px solid #fff;
  width: 100%;
}

.card-temperature {
  font-size: 1.5em;
  margin-top: 25px;
  text-align: left;
}

.card-row {
  align-items: baseline;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.card-temperature .small {
  font-size: .75em;
}

.card-city {
  font-size: 1.5em;
  margin-top: 8px;
  text-align: left;
}
.search {
    background-color: #201b2b;
    padding-top: 40px;
    height: 200px;
    width: 100%;
}

.search-city {
    display: block;
    margin: 20px auto;
    width: 30%;
    height: 40px;
    padding: 0 5px;
}

.search-city:focus {
    border: 1px solid white;
}

.search-city,
.get-weather {
    border-radius: 8px;
    border: none;
    background-color: rgb(223, 195, 223);
}

.get-weather {
    width: 10%;
    height: 30px;
    -webkit-transition: ease-in 0.2s;
    transition: ease-in 0.2s;
}

.get-weather:hover {
    background-color: rgb(82, 65, 82); 
    color: white;
    cursor: pointer;
}


.grid {
    display: grid;
    height: 100vh;
    grid-template-rows: 1fr 4fr 1fr;
}
.panel {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;  
    position: relative;
    background-color: rgb(82, 65, 82);
    background-image: url(/React-Weather-App/static/media/clouds.31ededdd.jpg);
    background-size: cover;
    margin: 0 auto;
    padding: 50px 0;
    width: 100%;
  }

  .caption {
    font-size: 3em;
    color: #201b2b;
    position: absolute;
    top: 50%; left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
